let menu = document.querySelector('.menu__links')
let line = document.querySelector('.menu__line')
let link = document.querySelector('.menu__link')

menu.addEventListener('click', (e) => {
    if ([...e.target.classList].includes('menu__link')) {
        document.querySelectorAll('.menu__link').forEach(elem => {
            elem.style.color = "#999"
        })
        e.target.style.color = "#fff"
        
        line.style.left = e.target.offsetLeft + 'px'
    }
})